"use client";

import { Box, Heading, Text, Table, Button,Flex, HStack, Badge } from "@chakra-ui/react";
import DashboardLayout from "../components/layout/DashboardLayout";
import { LuDownload, LuFilter } from "react-icons/lu";

export default function ReportsPage() {
  const reports = [
    { id: "#REP-001", name: "Monthly Revenue", date: "Oct 2023", status: "Generated" },
    { id: "#REP-002", name: "User Retention", date: "Oct 2023", status: "Generated" },
    { id: "#REP-003", name: "Q3 Tax Summary", date: "Sept 2023", status: "Pending" },
  ];

  return (
    <DashboardLayout>
      <Flex justify="space-between" align="center" mb={8} direction={{ base: "column", sm: "row" }} gap={4}>
        <Box>
          <Heading size="lg" mb={2}>Reports</Heading>
          <Text color="gray.500">Manage and download your generated data exports.</Text>
        </Box>
        <HStack>
          <Button variant="outline" size="sm" gap={2}><LuFilter /> Filter</Button>
          <Button colorPalette="teal" size="sm" gap={2}><LuDownload /> Export All</Button>
        </HStack>
      </Flex>

      <Box bg="white" _dark={{ bg: "gray.900" }} borderRadius="2xl" boxShadow="sm" overflow="hidden">
        <Table.Root variant="line">
          <Table.Header bg="gray.50" _dark={{ bg: "gray.800" }}>
            <Table.Row>
              <Table.ColumnHeader>ID</Table.ColumnHeader>
              <Table.ColumnHeader>Report Name</Table.ColumnHeader>
              <Table.ColumnHeader>Date</Table.ColumnHeader>
              <Table.ColumnHeader>Status</Table.ColumnHeader>
              <Table.ColumnHeader textAlign="right">Action</Table.ColumnHeader>
            </Table.Row>
          </Table.Header>
          <Table.Body>
            {reports.map((report) => (
              <Table.Row key={report.id} _hover={{ bg: "gray.50/50", _dark: { bg: "gray.800/50" } }}>
                <Table.Cell fontWeight="bold">{report.id}</Table.Cell>
                <Table.Cell>{report.name}</Table.Cell>
                <Table.Cell color="gray.500">{report.date}</Table.Cell>
                <Table.Cell>
                  <Badge colorPalette={report.status === "Generated" ? "green" : "orange"} variant="solid">
                    {report.status}
                  </Badge>
                </Table.Cell>
                <Table.Cell textAlign="right">
                  <Button variant="ghost" size="xs" colorPalette="teal">View</Button>
                </Table.Cell>
              </Table.Row>
            ))}
          </Table.Body>
        </Table.Root>
      </Box>
    </DashboardLayout>
  );
}